import { Injectable } from '@angular/core';
import { Helper, VStringService } from '@core/index';
import { ApiService } from '@core/services';
import * as moment from 'moment';

@Injectable({
  providedIn: 'root'
})
export class HandleReceiptService {
  currentDate = moment(new Date()).format('YYYY-MM-DD');
  data: any;
  isCopy: boolean = false;
  constructor(private apiService: ApiService) { }
  /*  */
  getAll(params?) {
    const includes = '&options[is_view_index]=1';
    const fields = Helper.danh_sach_truong.bien_lai;
    const filter_string = '&filter_groups[1][or]=true&filter_groups[1][filters][0][key]=originInvoiceExported&filter_groups[1][filters][0][value]=true&filter_groups[1][filters][0][operator]=eq&filter_groups[1][filters][1][key]=adjustment_type&filter_groups[1][filters][1][value]=1&filter_groups[1][filters][1][operator]=eq&filter_groups[1][filters][1][not]=true';
    let filter = filter_string;
    if (params) {
      filter += '&' + params;
    }
    const sort = '&sort[0][key]=status_order&sort[0][direction]=ASC';
    return this.apiService.getDataTable('/receipts', includes + fields + filter, sort);
  }
  /*  */
  ghiTam(data, license_number: boolean = false) {
    const url = license_number ? '/licenses?options[license_number]=1' : '/licenses';
    return this.apiService.post(url, { license: data });
  }
  /*  */
  xuatChungTu(data) {
    return this.apiService.post('/licenses-export', { license: data });
  }
  /*  */
  edit(id, data, license_number: boolean = false) {
    const url = license_number ? `/licenses/${id}?options[license_number]=1` : `/licenses/${id}`;
    return this.apiService.put(url, { license: data });
  }
  /*  */
  xuatChoKy(data) {
    if (data.id) {
      return this.apiService.put(`/licenses/${data.id}?options[license_number]=1&options[sign_later]=1`, { license: data });
    }
    return this.apiService.post('/licenses?options[license_number]=1&options[sign_later]=1', { license: data });
  }
  /*  */
  preview(data) {
    return this.apiService.post('/licenses-preview', {
      licenses: [
        data
      ]
    });
  }
  /*  */
  view(id) {
    return this.apiService.post('/licenses-view', { license: { ids: [id] } });
  }
  /*  */
  getInvoiceResolver(loaiHoaDon) {
    let _arrOut = [];
    _arrOut.push(
      this.apiService.get(`/templates?filter_groups[1][filters][1][key]=invoice_type&filter_groups[1][filters][1][value]=${loaiHoaDon}&filter_groups[1][filters][1][operator]=eq&filter_groups[2][filters][2][key]=status&filter_groups[2][filters][2][value]=HOAT_DONG&filter_groups[2][filters][2][operator]=eq`),
    );
    return _arrOut;
  }
  /*  */
  showVoucherResolver(id, loaiHoaDon) {
    let _arrOut = [];
    _arrOut.push(
      this.apiService.get('/licenses/' + id + '?includes[]=license_files&includes[]=license_options')
    );
    _arrOut.push(
      this.apiService.get(`/templates?filter_groups[1][filters][1][key]=invoice_type&filter_groups[1][filters][1][value]=${loaiHoaDon}&filter_groups[1][filters][1][operator]=eq&filter_groups[2][filters][2][key]=status&filter_groups[2][filters][2][value]=HOAT_DONG&filter_groups[2][filters][2][operator]=eq`),
    );
    return _arrOut;
  }
  /*  */
  delete(id) {
    return this.apiService.delete('/licenses/' + id);
  }
  /*  */
  xemChungTuImport(ids) {
    return this.apiService.post('/licenses-view', { license: { ids: ids } });
  }
  /*  */
  xuatChungTuPdf(ids) {
    return this.apiService.post('/licenses/export-file-zip-pdf', { license: ids });
  }
  /*  */
  xuatCTXml(ids) {
    return this.apiService.post('/licenses/export-file-zip-xml', { license: ids });
  }
 /*  */
  layDanhSachBienLai(ids) {
    const includes = '';
    let param = '&filter_groups[0][or]=true';
    ids.forEach((id, index) => {
      param += `&sort[0][key]=receipt_number&sort[0][direction]=ASC&filter_groups[0][filters][${index}][key]=id&filter_groups[0][filters][${index}][value]=${id}&filter_groups[0][filters][${index}][operator]=eq`;
    });
    return this.apiService.get('/receipts?' + includes + param);
  }

  sendMail(data) {
    return this.apiService.post('/license-send-mail', { licenses: data });
  }

  getVoucherProccess(license_id) {
    return this.apiService.get(`/license-process?includes[]=user&filter_groups[0][filters][0][key]=license_id&filter_groups[0][filters][0][value]=${license_id}&filter_groups[0][filters][0][operator]=eq&sort[0][key]=process_time&sort[0][direction]=DESC`);
  }

  getXmlString(data) {
    return this.apiService.post('/receipts/get-xml', { receipt: data });
  }

  cancelReceipt(data) {
    return this.apiService.post('/receipts-cancel', data);
  }
}