import { Component, OnInit, ViewChild } from '@angular/core';

import { Base64 } from 'js-base64';
import * as moment from 'moment';
import { DxDataGridComponent } from 'devextreme-angular';

import { Library, SocketService, AuthService, Helper, VStringService, VPreviewInvService, sEnum } from '@core/index';
import { HandleVoucherService } from '@modules/handle-voucher/shared';
import { BsModalRef } from 'ngx-bootstrap/modal/bs-modal-ref.service';
import { VatService } from '@modules/vat/shared';
import { InvoicesService } from '@core/library/invoice/invoices.service';
import { String, StringBuilder } from 'typescript-string-operations';
import { TranslateService } from '@ngx-translate/core';
import ArrayStore from 'devextreme/data/array_store';
import DataSource from 'devextreme/data/data_source';
import { pattern } from '@config/index';
import { ActivatedRoute, Router } from '@angular/router';
import { PaymentReceiptService } from '@modules/payment-receipt/shared/payment-receipt.service';
import { ViewInvoiceMultipageComponent } from '@shared/components';
import { BsModalService } from 'ngx-bootstrap';
import { NgxSpinnerService } from 'ngx-spinner';
import { SignatureOffComponent } from '@shared/index';
import { HandleReceiptService } from '@modules/handle-receipt/shared';
import { Location } from '@angular/common';
import { ViewInvoiceService } from '@shared/service/view-invoice.service';

@Component({
  selector: 'app-receipt-cancel',
  templateUrl: './receipt-cancel.component.html',
  styleUrls: ['./receipt-cancel.component.scss']
})
export class ReceiptCancelComponent implements OnInit {

  data: any;
  isView: any;
  configs: any;
  invoices: any;
  invoicesStore: ArrayStore;
  dataSource: DataSource;
  ioSignature: any;
  _language: string;
  @ViewChild(DxDataGridComponent) dataGrid: DxDataGridComponent;
  _translate: any;
  valueProgress: number = 0;
  errorImport: boolean = false;
  subXuatHoaDon: any;
  showPopup = false;
  totalInvoice: number;
  invoiceIndex = 0;
  tempKiFile = 0;
  tempTemplate: any;
  nodeSign: any;
  countReport: any;
  emailPattern: any = pattern.email;
  infoDangKy: any;
  isTT78: boolean = false;
  dataInfor: any;
  checkSubmit: any;
  format = (value) => {
    let percent = Math.round(value * 100);
    return this._translate.NOTIFY.finish + ": " + percent + '%';
  }
  constructor(
    public bsModalImportRef: BsModalRef,
    private socketService: SocketService,
    private handleReceiptService: HandleReceiptService,
    private authService: AuthService,
    private vatService: VatService,
    public translate: TranslateService,
    private router: Router,
    private paymentRService: PaymentReceiptService,
    private modalService: BsModalService,
    private modalImportService: BsModalService,
    private spinner: NgxSpinnerService,
    public bsModalViewModel: BsModalRef,
    private vPreviewInvService: ViewInvoiceService,
  ) {
    this._language = this.authService.getLanguage();
    this._translate = this.authService.getTranslate();
    translate.use(this._language);
  }
  ngOnInit() {
    this.invoicesStore = new ArrayStore({
      key: 'id',
      data: this.data
    });
    this.dataSource = new DataSource({
      store: this.invoicesStore
    });
    if (this.infoDangKy && this.infoDangKy.id) {
      if (this.infoDangKy.status == "CHAP_NHAN")
        this.isTT78 = true;
    }
    // this.invoicesStore.load().then((dataSource) => {
    //   dataSource.forEach((item, index) => {
    //     item.doc_relate_symbol = this.createDocRelateNumber();
    //   });
    // });
    this.connectWebsocket();
  }
  createDocRelateNumber() {
    let number_report = "";
    if (this.countReport < 10)
      number_report = '0' + this.countReport + '/' + new Date().getFullYear() + '/BB-HBH';
    else
      number_report = this.countReport + '/' + new Date().getFullYear() + '/BB-HBH';
    this.countReport++;
    return number_report;
  }

  connectWebsocket() {
    this.ioSignature = this.socketService.lapHoaDonImport().subscribe(message => {
      let data: any = message;
      if (data.Status == 'success') {
        if (data.SvcName == "efy_Sign") {
          let i = this.invoiceIndex;
          if (data.Data.Name == 'hoadon.xml') {
            this.goiApiXuatHoaDon(data.Data.Content, this.invoices[i]);
          } else if (data.Data.Name == 'ten_filek_the_trung-dc-nhi.xml') {
            this.invoices[i].issuer_name = data.Data.IssuerName;
            this.xuLiApiKhiXuat(this.invoices[i]);
          } else {
            this.invoices[i].invoice_files.forEach(item => {
              if (item.file_name == data.Data.Name) {
                item.file_content = data.Data.Content;
              }
            });
            this.tempKiFile++;
            if (this.tempKiFile == this.invoices[i].invoice_files.length) {
              this.xuLiApiKhiXuat(this.invoices[i]);
            }
          }
        }
      } else {
        this.showPopup = false;
        this.errorImport = true;
        Library.notify(data.Description, 'error');
      }
    });
  }

  _validGrid() {
    let invalid = true;
    this.invoicesStore.load().then((dataSource) => {
      dataSource.forEach((item, index) => {
        item.doc_relate_date = item.doc_relate_date ? moment(item.doc_relate_date).format('YYYY-MM-DD') : item.doc_relate_date;
        item.destroy_date = item.destroy_date ? moment(item.destroy_date).format('YYYY-MM-DD') : item.destroy_date;
        if (!item.destroy_date && invalid) {
          Library.notify(this._translate.CONTROLL.LABEL.cancel_date + " " + this._translate.VALIDATION.required, 'error');
          this.dataGrid.instance.focus(this.dataGrid.instance.getCellElement(index, 'destroy_date'));
          invalid = false;
          return false;
        } else {
          let currentDate = moment(new Date()).format('YYYY-MM-DD');
          if (moment(item.destroy_date) > moment(currentDate) && invalid) {
            Library.notify(this._translate.NOTIFY.date_cancel_invoice_not_greater_date_current, 'error');
            this.dataGrid.instance.focus(this.dataGrid.instance.getCellElement(index, 'destroy_date'));
            invalid = false;
            return false;
          }
          let receipt_date = moment(item.receipt_date).format('YYYY-MM-DD');
          if (moment(item.destroy_date) < moment(receipt_date) && invalid) {
            Library.notify(this._translate.THONG_TIN.ngay_huy_lon_hon_ngay_bien_lai, 'error');
            this.dataGrid.instance.focus(this.dataGrid.instance.getCellElement(index, 'destroy_date'));
            invalid = false;
            return false;
          }
        }
        if (!item.destroy_reason && invalid) {
          Library.notify(this._translate.CONTROLL.LABEL.reason_cancels + " " + this._translate.VALIDATION.required, 'error');
          this.dataGrid.instance.focus(this.dataGrid.instance.getCellElement(index, 'destroy_reason'));
          invalid = false;
          return false;
        }
        if (item.payer_email && !this.emailPattern.test(item.payer_email) && invalid) {
          Library.notify('Email ' + this._translate.VALIDATION.not_pattern, 'error');
          this.dataGrid.instance.focus(this.dataGrid.instance.getCellElement(index, 'payer_email'));
          invalid = false;
          return false;
        }
      });
    });
    return invalid;
  }

  xoaBo() {
    if (!InvoicesService.checkTimeExportInv(this.configs.system_parameter)) {
      Library.notify(String.Format(this._translate.NOTIFY.time_export_inv, moment(this.configs.system_parameter.time_start).format('HH:mm'), moment(this.configs.system_parameter.time_end).format('HH:mm')), 'error');
      return;
    }
    setTimeout(() => {
      if (this._validGrid()) {
        Library.confirmVA(this._translate.CONFIRM.acction_function, this._translate.NOTIFY.notify, () => {
          this.showPopup = true;
          this.invoicesStore.load().then((dataSource) => {
            if (dataSource.length > 0) {
              this.invoices = dataSource.filter(p => p.adjustment_type != '7');
              this.totalInvoice = this.invoices.length;
              this.invoiceIndex = 0;
              this.tempKiFile = 0;
              this.xuatHoaDon();
            }
          });
        });
      }
    }, 300);
  }

  xuatHoaDon() {
    let i = this.invoiceIndex;
    if (this.configs.integrate_parameter.concentrate_signature.is_concentrate_signature
      || (this.configs.integrate_parameter.remote_signing && this.configs.integrate_parameter.remote_signing.is_remote_signing)) {
      this.xuLiApiKhiXuat(this.invoices[i]);
    } else
      this.socketService.kyBienLai(() => this.connectWebsocket(), 'PHRlbXBJbnZvaWNlPjwvdGVtcEludm9pY2U+', 'ten_filek_the_trung-dc-nhi.xml', '//tempInvoice');
  }
  xuLiApiKhiXuat(invoice) {
    let tempTemplate = invoice.template;
    delete invoice.template;
    let xmlInvoice = "";
    let data = {
      id: invoice.id,
      destroy_reason: invoice.destroy_reason,
      destroy_date: invoice.destroy_date,
      adjustment_type: "7",
      email: invoice.buyer_email,
    };
    let isNewXml = 0;
    let sub = this.handleReceiptService.getXmlString(data).subscribe(rs => {
      invoice.template = tempTemplate;
      if (rs.status == 'success') {
        xmlInvoice = rs.data.receipt.xml_string;
        let sign_date = moment(rs.data.receipt.receipt_sign_date).format('YYYY-MM-DD HH:mm:ss');
        isNewXml = rs.data.is_new_xml;
        if (isNewXml)
          this.nodeSign = "//HDon/DSCKS/NBan";
        else
          this.nodeSign = "//inv:transaction/inv:invoices/inv:invoice";
        if (xmlInvoice != "") {
          if (this.configs.integrate_parameter.concentrate_signature.is_concentrate_signature
            || (this.configs.integrate_parameter.remote_signing && this.configs.integrate_parameter.remote_signing.is_remote_signing))
            this.goiApiXuatHoaDon(xmlInvoice, invoice);
          else {
            this.xuLyKiFile(xmlInvoice, sign_date);
          }
        }
      } else {
        this.showPopup = false;
        this.errorImport = true;
        Library.notify(rs.message, 'error');
      }
    }, err => { }, () => {
      sub.unsubscribe();
    });
  }

  xuLyKiFile(xmlInvoice, signDate) {
    this.socketService.kyBienLai(() => this.connectWebsocket(), xmlInvoice, 'hoadon.xml', this.nodeSign, signDate);
  }

  goiApiXuatHoaDon(xml_string, invoice) {
    let percentComplete = 100 / this.totalInvoice;
    // Helper.dowloadFile('hoadon.xml', 'xml', xml_string);
    this.subXuatHoaDon = this.handleReceiptService.cancelReceipt({
      receipt: {
        id: invoice.id,
        destroy_reason: invoice.destroy_reason,
        destroy_date: invoice.destroy_date,
        xml_string: xml_string
      }
    }).subscribe(rs => {
      if (rs.status == 'success') {
        this.valueProgress += percentComplete;
        this.tempKiFile = 0;
        invoice.adjustment_type = '7';
        if (this.invoiceIndex < this.totalInvoice - 1) {
          this.invoiceIndex++;
          this.xuatHoaDon();
        } else if (this.invoiceIndex == this.totalInvoice - 1) {
          Library.notify(this._translate.THONG_TIN.huy_bien_lai_thanh_cong, 'success');
          this.isView = true;
          setTimeout(() => {
            this.showPopup = false;
          }, 300);
        }
      } else {
        this.showPopup = false;
        this.errorImport = true;
        Library.notify(rs.message, 'error');
      }
    }, err => { }, () => {
      this.subXuatHoaDon.unsubscribe();
    });
  }

  dinhKemFile(evt) {
    evt.srcElement.firstChild.click();
    // document.getElementById("file-dinh-kem").click();
    // console.log(e)

  }
  choseFile(evt) {
    //evt.srcElement.firstChild.click();
    document.getElementById("file-input").click();
  }
  fileChanged(e) {
    setTimeout(() => {
      //let rowIndex = e.target.dataset.rowindex;
      let fileAttachData: Array<any> = [];
      let fileReader = new FileReader();
      let file = e.target.files[0];
      let pattFile = /(\.xlsx|\.docx|\.doc|\.pdf|\.xls|\.xml)$/i;
      // console.log(file)
      if (pattFile.test(file.name)) {
        fileReader.readAsDataURL(file);
        fileReader.onload = (e) => {
          let base64result = fileReader.result.toString().split(',')[1];
          fileAttachData.unshift({
            type: "DK",
            file_name: file.name,
            file_content: base64result,
          });
          // if (document.getElementById('list-file')) {
          //   document.getElementById('list-file').scrollTop = 0;
          // }
          let _lstAttach: any = [];
          _lstAttach = this.dataSource[0]["invoice_files"];
          if (_lstAttach == null) {
            _lstAttach = [];
          } else {
            _lstAttach.push(fileAttachData[0]);
          }
          this.dataSource[0]["invoice_files"] = _lstAttach;
          // this.dataSource[rowIndex]["report_cancel_invoice_file_name"] = file.name;
          //this.dataSource.reload();
        }
      } else {
        Library.notify("File bn chn khng ng nh dng xls, xlsx, doc, docx, pdf, xml", "error");
      }
    }, 200);
  }
  onDinhKemFile(e) {
    let rowIndex = e.target.dataset.rowindex;
    let fileAttachData: Array<any> = [];
    let fileReader = new FileReader();
    let file = e.target.files[0];
    let pattFile = /(\.xlsx|\.docx|\.doc|\.pdf|\.xls|\.xml)$/i;
    // console.log(file)
    if (pattFile.test(file.name)) {
      fileReader.readAsDataURL(file);
      fileReader.onload = (e) => {
        let test = fileReader.result;
        let base64result = fileReader.result.toString().split(',')[1];
        fileAttachData.unshift({
          type: "DK",
          file_name: file.name,
          file_content: base64result,
        });
        if (document.getElementById('list-file')) {
          document.getElementById('list-file').scrollTop = 0;
        }
        this.invoicesStore.load().then((dataSource) => {
          dataSource.forEach((item, index) => {
            if (index == rowIndex)
              item.invoice_files = fileAttachData;
          });
        });
        // this.dataSource[rowIndex]["invoice_files"] = fileAttachData;
        // this.dataSource[rowIndex]["report_cancel_invoice_file_content"] = base64result;
        // this.dataSource[rowIndex]["report_cancel_invoice_file_name"] = file.name;
      }
    } else {
      Library.notify("File bn chn khng ng nh dng xls, xlsx, doc, docx, pdf, xml", "error");
    }
  }
  removeFile(i) {
    let _lstAttach: any = [];
    _lstAttach = this.dataSource[0]["invoice_files"];
    _lstAttach.splice(i, 1);
  }
  downloadFile(data) {
    var re = /(?:\.([^.]+))?$/;
    var fileType = re.exec(data.file_name)[1];
    Helper.dowloadFile(data.file_name, fileType, data.file_content);
  }

  xuatMauBienBan(data) {
    if (this._validGrid()) {
      Library.showloading();
      let inv = Object.assign({}, data);
      inv.adjustment_type = "1";
      inv.auto_value_price = inv.auto_value_price == null ? false : inv.auto_value_price;
      inv.invoice_files = [];
      inv.doc_relate_date = moment(data.doc_relate_date).format('YYYY-MM-DD');
      this.vatService.xuatMauBienBan(inv).subscribe(rs => {
        if (rs.status == 'success') {
          let file = rs.data.invoice;
          Helper.dowloadFile(file.file_name, 'xlsx', file.file_content);
        } else {
          Library.notify(rs.message, 'error');
        }
      }, err => { }, () => {
        Library.hideloading();
      });
    }
  }
  onImportShowing(e) {
    this.valueProgress = 0;
  }
  onImportHiding(e) {
    let r = true;
    if (this.valueProgress < 100 && this.errorImport == false) {
      r = confirm(this._translate.CONFIRM.sign_stop);
    }
    if (r) {
      if (this.subXuatHoaDon) {
        this.subXuatHoaDon.unsubscribe();
      }
      this.valueProgress = 0;
    } else {
      e.cancel = true;
    }
  }
  dong() {
    this.bsModalImportRef.content.successSaveItem = "close";
    this.bsModalImportRef.hide();
  }
  onEditorPreparing(e) {
    if (e.parentType === 'dataRow') {
      if (e.dataField === 'destroy_date' || e.dataField === 'destroy_reason') {
        if (this.isView || e.row.data.adjustment_type == "7")
          e.editorOptions.disabled = true;
      }
    }
  }
  onCellPrepared(e) {
    if (e.rowType == "data") {
      /* Bi  cc trng bt buc */
      if (e.column.dataField == "destroy_date") {
        if (!e.data.destroy_date) {
          e.cellElement.style.background = "red url('assets/img/icons/fa-remove.png') no-repeat 2% center";
          e.cellElement.title = this._translate.CONTROLL.LABEL.cancel_date + " " + this._translate.VALIDATION.required;
        }
        else {
          let currentDate = moment(new Date()).format('YYYY-MM-DD');
          let receipt_date = moment(e.data.receipt_date).format('YYYY-MM-DD');
          let destroy_date = moment(e.data.destroy_date).format('YYYY-MM-DD');
          if (moment(destroy_date) > moment(currentDate)) {
            e.cellElement.style.background = "red url('assets/img/icons/fa-remove.png') no-repeat 2% center";
            e.cellElement.title = this._translate.NOTIFY.date_cancel_invoice_not_greater_date_current;
          }
          if (moment(destroy_date) < moment(receipt_date)) {
            e.cellElement.style.background = "red url('assets/img/icons/fa-remove.png') no-repeat 2% center";
            e.cellElement.title = this._translate.THONG_TIN.ngay_huy_lon_hon_ngay_bien_lai;
          }
        }
      }
      if (e.column.dataField == "destroy_reason") {
        if (!e.data.destroy_reason) {
          e.cellElement.style.background = "red url('assets/img/icons/fa-remove.png') no-repeat 2% center";
          e.cellElement.title = this._translate.CONTROLL.LABEL.reason_cancels + " " + this._translate.VALIDATION.required;
        }
      }
      if (e.column.dataField == "payer_email") {
        if (e.data.payer_email && !this.emailPattern.test(e.data.payer_email)) {
          e.cellElement.style.background = "red url('assets/img/icons/fa-remove.png') no-repeat 2% center";
          e.cellElement.title = 'Email ' + this._translate.VALIDATION.not_pattern;
        }
      }
    }
  }
  
  viewDetailReceipt(data) {
    if (data) {
      this.dataInfor = data;
      let ids = [];
      ids.push(data.id);
      if (this.configs.system_parameter.view_multi_invoice) {
        let data = this.paymentRService.taodulieuXemMau(ids);
        data['configs'] = sEnum.cauhinhxemmau.hoadon;
        this.vPreviewInvService.xemMau(data);
      }
      else {
        const initialState = {
          items: ids,
          isXuatHD: false,
          hinhthucxuat: '',
          typeAction: sEnum.typeCode.xem_bien_lai
        };
        this.vPreviewInvService.xemMauPhanTrang(initialState);
      }
    } else {
      Library.notify(this._translate.NOTIFY.no_record_selected, 'error');
    }
  }
  setUrlNavigate() {
    const url = window.location.pathname;
    if (sessionStorage.hasOwnProperty('urlIndex')) {
      if (sessionStorage.getItem('urlIndex') !== null) {
        sessionStorage.setItem('urlIndex', url);
      } else {
        sessionStorage.setItem('urlIndex', url);
      }
    } else {
      sessionStorage.setItem('urlIndex', url);
    }
  }
}
