import { AfterViewInit, Component, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { AuthService, Helper, Library, VPreviewInvService, sEnum, VStringService, SocketService, VSelectBoxService } from '@core/index';
import { BsModalRef } from 'ngx-bootstrap/modal/bs-modal-ref.service';
import { BsModalService } from 'ngx-bootstrap/modal';
import { ViewInvoiceMultipageComponent } from '@shared/index';
import { Subscription } from 'rxjs/Subscription';
import { NgxSpinnerService } from 'ngx-spinner';
import { SignatureOffComponent, SendListVoucherComponent, LogProcessVoucherComponent } from '@shared/index';
import { VoucherCancelComponent } from '@modules/handle-voucher/components/voucher-cancel/voucher-cancel.component';
import { HandleReceiptService } from '@modules/handle-receipt/shared';
import { PaymentReceiptService } from '@modules/payment-receipt/shared/payment-receipt.service';
import { ReceiptCancelComponent } from '../receipt-cancel/receipt-cancel.component';
import { ReceiptSearchComponent } from '@shared/components/receipt-search-advanced/search-advanced.component';
import { ViewInvoiceService } from '@shared/service/view-invoice.service';
import { InvoiceService } from '@shared/service/invoice.service';
import { DxDataGridComponent } from 'devextreme-angular';
@Component({
  selector: 'app-index',
  templateUrl: './index.component.html',
  styleUrls: ['./index.component.scss']
})
export class IndexComponent implements OnInit, AfterViewInit {
  @ViewChild(ReceiptSearchComponent) searchComponent: ReceiptSearchComponent;
  @ViewChild(DxDataGridComponent) dataGrid: DxDataGridComponent;
  selectedRows: Array<any> = [];
  selectedItems: Array<any> = [];
  configs: any = {};
  pageSelect: any;
  idRow: any = undefined;
  clickCurent: any = undefined;
  dataSource: any;
  _translate: any;
  paramSearch: any;
  extras = [];
  dataInfor: any;
  bsModalRef: BsModalRef;
  bsModalImportRef: BsModalRef;
  bsModalSignRef: BsModalRef;
  ioSignature: Subscription;
  checkSubmit: any;

  valueProgress: number = 0;
  errorImport: boolean = false;
  subXuatHoaDon: any;
  showPopup = false;
  totalInvoice: number;
  invoiceIndex = 0;
  tempKiFile = 0;
  daXong: boolean = false;
  ganXong: boolean = false;
  invoices = [];
  isExportFailed: boolean = false;
  isXuLyBienlai: boolean = false;
  textHienThiLuoi: any;
  currentUser: any;
  objStatusBtn = {};
  format = (value) => {
    let percent = Math.round(value * 100);
    return this._translate.NOTIFY.finish + ": " + percent + '%';
  }
  adjustmentTypes = [];
  constructor(
    private authService: AuthService,
    public translate: TranslateService,
    private router: Router,
    private receiptService: HandleReceiptService,
    private modalService: BsModalService,
    private modalImportService: BsModalService,
    private socketService: SocketService,
    private paymentRService: PaymentReceiptService,
    private vPreviewInvService: ViewInvoiceService,
    private invoiceService: InvoiceService,
    private spinner: NgxSpinnerService) {
    this._translate = this.authService.getTranslate();
    translate.use(this.authService.getLanguage());
    this.extras = [{
      id: '1',
      name: this._translate.CONTROLL.BUTTON.extend,
      items: [
        // {
        //   id: "NAP_HD_TU_EXCEL",
        //   name: this._translate.CONTROLL.BUTTON.import_invoice_from_excel,
        //   icon: this._translate.ICONS.excel
        // },

        {
          id: 'SUA',
          name: this._translate.CONTROLL.BUTTON.edit,
          icon: this._translate.ICONS.edit
        },
        {
          id: 'XOA',
          name: this._translate.CONTROLL.BUTTON.delete,
          icon: this._translate.ICONS.delete
        },
        {
          id: 'XEM',
          name: this._translate.CONTROLL.BUTTON.view_voucher,
          icon: this._translate.ICONS.view
        },
        {
          id: 'GUI_BEN_MUA',
          name: this._translate.CONTROLL.LABEL.send_invoice,
          icon: this._translate.ICONS.send_email
        },
        {
          id: 'TAI_CT_PDF',
          name: this._translate.CONTROLL.BUTTON.tai_ct_pdf,
          icon: this._translate.ICONS.download
        },
        {
          id: "TAI_CT_XML",
          name: this._translate.CONTROLL.BUTTON.tai_ct_xml,
          icon: this._translate.ICONS.xml
        }
      ]
    }];
  }
  ngAfterViewInit(): void {
    if (this.searchComponent.isViewProcessStatus)
      this.searchComponent.isViewProcessStatus = false;
  }

  ngOnInit() {
    if (this.searchComponent)
      this.searchComponent.isViewProcessStatus = false;
    this.textHienThiLuoi = "Khng c d liu";
    this.configs = this.authService.getConfigs();
    if (this.configs.system_parameter.select_invoice_current_page)
      this.pageSelect = "page";
    else
      this.pageSelect = "allPages";
    this.currentUser = this.authService.getCurrentUser();
    const listpermissions = this.currentUser.user.permissions.split(',');
    let quyenxulybienlai = listpermissions.filter(p => p === 'xu_ly_bien_lai')[0];
    if (quyenxulybienlai)
      this.isXuLyBienlai = true;
    if (!this.isXuLyBienlai) {
      this.textHienThiLuoi = "Bn khng c quyn thc hin chc nng ny";
    }
    this.adjustmentTypes = VSelectBoxService._adjustmentTypes(this._translate);
    this.adjustmentTypes = this.adjustmentTypes.filter(p=>p.code == '1' || p.code == '7');
    this.adjustmentTypes.forEach(item => {
      item.name = item.name.replace("Ha n", "Bin lai");
    });
  }
  
  selectionChanged(data: any) {
    this.hidenDropdownList();
    this.selectedItems = data.selectedRowsData;
  }
  hidenDropdownList() {
    if (this.clickCurent) {
      document.getElementById(this.clickCurent).classList.remove('show');
    }
    if (this.idRow) {
      document.getElementById(this.idRow).classList.remove('show');
    }
  }
  loadList(params?) {
    this.dataSource = {};
    if (this.isXuLyBienlai) {
      if (params) {
        this.dataSource.store = this.receiptService.getAll(params);
      } else {
        this.dataSource.store = this.receiptService.getAll();
      }
    }
  }
  getSearch(e) {
    this.paramSearch = e;
    this.loadList(e);

  }
  onRowPrepared(e) {
    if (e.rowType === 'data') {
      switch (e.data.status) {
        case 'DA_XUAT':
          break;
        case 'GHI_TAM':
          if (e.data.license_number) {
            e.rowElement.style.color = 'red';
          } else {
            e.rowElement.style.color = '#DEA960';
          }
          break;
        case 'GIU_SO':
          e.rowElement.style.color = '#008B8B';
          break;
        default:
          break;
      }
      if (e.data.adjustment_type === '2' || e.data.adjustment_type === '7' || e.data.adjustment_type === '6') {
        const eles = e.rowElement.getElementsByTagName('td');
        for (let i = 0; i < eles.length; i++) {
          if (i !== 9 && i !== 1) {
            eles[i].style.textDecoration = 'line-through';
          }
        }
      }
    }
  }
  
  view(data) {
    if (this.selectedItems.length > 0 || data) {
      const _lst = this.selectedItems.filter(p => p.status === 'DA_XUAT');
      if (_lst.length > 0 || data) {
        if (this.selectedItems.length === 1) {
          this.dataInfor = this.selectedItems[0];
        } else {
          this.dataInfor = data;
        }
        let ids = [];
        if (data) {
          ids.push(data.id);
        } else {
          ids = _lst.map(e => e.id);
        }
        if (this.configs.system_parameter.view_multi_invoice) {
          let data = this.paymentRService.taodulieuXemMau(ids);
          data['configs'] = sEnum.cauhinhxemmau.hoadon;
          this.vPreviewInvService.xemMau(data);
        }
        else {
          const initialState = {
            items: ids,
            isXuatHD: false,
            hinhthucxuat: '',
            typeAction: sEnum.typeCode.xem_bien_lai
          };
          this.vPreviewInvService.xemMauPhanTrang(initialState);
        }
      } else {
        Library.notify(this._translate.NOTIFY.no_record_selected, 'error');
      }
    } else {
      Library.notify(this._translate.NOTIFY.no_record_selected, 'error');
    }
  }
  setUrlNavigate() {
    const url = window.location.pathname;
    if (sessionStorage.hasOwnProperty('urlIndex')) {
      if (sessionStorage.getItem('urlIndex') !== null) {
        sessionStorage.setItem('urlIndex', url);
      } else {
        sessionStorage.setItem('urlIndex', url);
      }
    } else {
      sessionStorage.setItem('urlIndex', url);
    }
  }
  edit(data) {
    let rowData = null;
    if (data) {
      if (VStringService._getValueToString(data.status) == sEnum.statusInv.DaXuat)
        Library.notify(this._translate.NOTIFY.you_cannot_edit_an_exported_voucher, 'error');
      else
        rowData = data;
    }
    else {
      if (this.selectedItems.length > 0) {
        if (this.selectedItems.length == 1) {
          if (VStringService._getValueToString(this.selectedItems[0].status) == sEnum.statusInv.DaXuat)
            Library.notify(this._translate.NOTIFY.you_cannot_edit_an_exported_voucher, 'error');
          else
            rowData = this.selectedItems[0];
        }
        else {
          Library.notify(this._translate.NOTIFY.only_choose_voucher_edit, 'error');
        }
      }
    }
    if (rowData) {
      this.router.navigate([`/system/voucher-tax/${VStringService._getValueToString(rowData.id)}`]);
    }
  }
  delete(data) {
    if (this.selectedItems.length || data) {
      const status = '';
      let ids = '';
      if (data) {
        ids = data.id;
      } else {
        ids = this.selectedRows.join(',');
      }
      const result = Library.confirm(this._translate.CONFIRM.delete, this._translate.NOTIFY.notify);
      result.then((dialogResult) => {
        if (dialogResult) {
          Library.showloading();
          this.receiptService.delete(ids).subscribe(rs => {
            if (rs.status === 'success') {
              Library.notify(this._translate.NOTIFY.delete_success, 'success');
              this.loadList(this.paramSearch);
            } else {
              Library.notify(rs.message, 'error');
            }
          }, err => {
          }, () => {
            Library.hideloading();
          });
        }
      });
    }
  }
  kyHangLoat(data) {

  }
  guibenmua(data) {
    if (this.clickCurent) {
      document.getElementById(this.clickCurent).classList.remove('show');
    }
    let check = true;
    if (data) {
      if (data.status !== 'DA_XUAT') {
        check = false;
      }
    } else {
      if (this.selectedItems.filter(p => p.status === 'DA_XUAT').length === 0) {
        check = false;
      }
    }

    if (check && (this.selectedItems.length || data)) {
      Library.showloading();
      let ids = [];
      if (data) {
        ids.push(data.id);
      } else {
        ids = this.selectedItems.filter(p => p.status === 'DA_XUAT').map(p => p.id);
      }
      this.receiptService.layDanhSachBienLai(ids).subscribe((rs: any) => {
        if (rs.status === 'success') {
          const initialState = {
            licenseList: rs.data.licenses,
            configs: this.configs
          };
          this.bsModalSignRef = this.modalService.show(SendListVoucherComponent, {
            backdrop: 'static',
            keyboard: false,
            class: 'modal-lg full-modal',
            initialState
          });
          const subModal = this.modalService.onHide.subscribe(() => {
            if (this.bsModalSignRef.content.successSaveItem) {
              this.selectedItems = [];
              this.selectedRows = [];
              this.loadList(this.paramSearch);
              subModal.unsubscribe();
            }
          });
        } else {
          Library.notify(this._translate.NOTIFY.an_error_occurred, 'error');
        }

      }, err => {
      }, () => {
        Library.hideloading();
      });
    } else {
      Library.notify(this._translate.NOTIFY.no_invoice_selected, 'error');
    }
  }
  taiHDXml(data) {
    if (data) {
      let ids = [];
      if (data.status != sEnum.statusInv.DaXuat) {
        Library.notify(this._translate.NOTIFY.invoice_export_status, 'error');
        return;
      }
      ids.push(data.id);
      Library.showloading();
      let sub = this.receiptService.xuatCTXml(ids).subscribe(rs => {
        if (rs.status == "success") {
          Helper.dowloadFile(rs.data.file_name, 'zip', rs.data.file_content);
        } else {
          Library.notify(rs.message, "error");
        }
      }, err => { }, () => {
        sub.unsubscribe();
        Library.hideloading();
      });
    }
    else {
      Library.notify(this._translate.NOTIFY.please_choose_voucher_exported, 'error');
      return;
    }
  }
  taiHDPdf() {
    if (this.selectedItems.length > 0) {
      const _lst = this.selectedItems.filter(p => p.status == sEnum.statusInv.DaXuat);
      if (_lst.length > 0) {
        this.invoices = _lst.map(e => e.id);
        this.showPopup = true;
        this.totalInvoice = Math.floor(this.invoices.length / 10) + 1;
        this.invoiceIndex = 0;
        this.tempKiFile = 0;
        this.xulyTaiFilePdf();
      }
      else {
        Library.notify(this._translate.NOTIFY.please_choose_voucher_exported, 'error');
        return;
      }
    }
    else {
      Library.notify(this._translate.NOTIFY.please_choose_voucher_exported, 'error');
      return;
    }
  }
  xulyTaiFilePdf() {
    let ids = this.iterator(this.invoices, 10, this.invoiceIndex);

    let sub = this.receiptService.xuatChungTuPdf(ids).subscribe(rs => {
      let isSuccess = false;
      this.ganXong = true;
      this.tempKiFile = 0;
      if (rs.status == 'success') {
        Helper.dowloadFile(rs.data.file_name, 'zip', rs.data.file_content);
        isSuccess = true;
      } else {
        this.isExportFailed = true;
      }
      this.continuteExportInvoice(isSuccess);
    }, err => { }, () => {
      sub.unsubscribe();
      Library.hideloading();
    });
  }
  continuteExportInvoice(isSuccess) {
    let percentComplete = 100 / this.totalInvoice;
    this.valueProgress += percentComplete;
    if (this.invoiceIndex == this.totalInvoice - 1) {
      if (!this.isExportFailed && isSuccess)
        Library.notify("Ti chng t thnh cng", 'success', 5000);
      else {
        // Danh sch li
      }
      this.daXong = true;
      setTimeout(() => {
        this.showPopup = false;
      }, 300);
    }
    else if (this.invoiceIndex < this.totalInvoice - 1) {
      this.invoiceIndex++;
      this.xulyTaiFilePdf();
    }
  }
  createLicense() {
    this.router.navigate(['system/voucher-tax/create']);
  }
  iterator(a, take, skip) {
    let current = skip * 10;
    let l = a.length;
    let end = current + take;
    let part = a.slice(current, end);
    current = end < l ? end : 0;
    return part;
  }
  itemExtrasClick(e) {
    if (e.itemData.id === 'KY_SO') {
      this.kyHangLoat(null);
    } else if (e.itemData.id === 'SUA') {
      this.edit(null);
    } else if (e.itemData.id === 'XOA') {
      this.delete(null);
    } else if (e.itemData.id === 'XEM') {
      this.view(null);
    } else if (e.itemData.id === 'GUI_BEN_MUA') {
      this.guibenmua(null);
    } else if (e.itemData.id === 'LIST_INVOICE_BUY') {
      this.guibenmua(null);
    } else if (e.itemData.id === 'DETAILS_OF_EACH_GOODS_AND_SERVICE') {
      this.guibenmua(null);
    } else if (e.itemData.id === 'ADD_MULTIPLE_INVOICE') {
      this.guibenmua(null);
    } else if (e.itemData.id == "TAI_CT_PDF") {
      this.taiHDPdf();
    }
  }
  sendVoucher(id) {
    Library.showloading();
    let ids = [];
    ids.push(id);
    let sub = this.receiptService.layDanhSachBienLai(ids).subscribe(rs => {
      if (rs.status == 'success') {
        const initialState = {
          licenses: rs.data.licenses,
        };
        this.bsModalRef = this.modalService.show(LogProcessVoucherComponent, {
          class: 'modal-lg full-modal', initialState, backdrop: 'static',
          keyboard: false
        });
        this.modalService.onHide.subscribe(() => {
          if (this.bsModalRef.content.successSaveItem) {
            this.loadList(this.paramSearch);
          }
        });
      } else {
        Library.notify(rs.message, 'error');
      }
    }, err => {
    }, () => {
      Library.hideloading();
      sub.unsubscribe();
    });
  }
  trangthaihoadon(data) {
    switch (data.status) {
      case 'DA_XUAT':
        break;
      case 'GHI_TAM':
        if (data.license_number) {
          return this._translate.GRID.INVOICE.signed_error;
        } else {
          return this._translate.CONTROLL.BUTTON.save_temporary;
        }
      case 'GIU_SO':
        return this._translate.GRID.INVOICE.wait_for_sign;
      default:
        break;
    }
  }
  editNotSelect(row) {
    if (row.status === 'DA_XUAT') {
      Library.notify(this._translate.THONG_TIN.ban_khong_the_sua_bien_lai_da_xuat, 'error');
    } else {
      this.router.navigate([`/system/payment-receipt/${row.id}`]);
    }
  }
  viewDetailReceipt(row) {
    this.router.navigate([`/system/payment-receipt/${row.id}`]);
  }
  cancelReceipt() {
    let lstInvoice = this.selectedItems.filter(p => p.status == 'DA_XUAT');
    if (lstInvoice && lstInvoice.length > 0) {
      let _lstCancel = lstInvoice.filter(p => p.adjustment_type == '7');
      if (_lstCancel && _lstCancel.length > 0) {
        Library.notify(this._translate.THONG_TIN.chon_bien_lai_chua_bi_xoa_bo_hoac_chua_bi_thay_the, 'error');
      }
      else {
        let ids = [];
        lstInvoice = lstInvoice.sort((invA, invB) => {
          return <any>new Date(invB.receipt_date) - <any>new Date(invA.receipt_date) || invB.receipt_number.localeCompare(invA.receipt_number);
        });

        ids = lstInvoice.map(e => e.id);
        Library.showloading();
        let isView = false;
        const sub = this.receiptService.layDanhSachBienLai(ids).subscribe(rs => {
          let lstInvoice = rs.data.receipts.filter(p => p.adjustment_type == '7');
          if (lstInvoice && lstInvoice.length > 0)
            isView = true;
          let lstInv = [];
          ids.forEach(id => {
            let inv = rs.data.receipts.filter(p => p.id == id)[0];
            lstInv.push(inv);
          });
          const initialState = {
            isView: isView,
            data: lstInv,
            configs: this.configs,
          };
          this.bsModalRef = this.modalService.show(ReceiptCancelComponent, {
            class: 'modal-lg full-width', initialState, backdrop: 'static',
            keyboard: false
          });
          this.modalService.onHide.subscribe(() => {
            if (this.bsModalRef.content.successSaveItem) {
              this.loadList(this.paramSearch);
            }
          });
        }, err => {
        }, () => {
          Library.hideloading();
          sub.unsubscribe();
        });
      }
    }
    else
      Library.notify('Bn khng th hy ha n ny', 'error');

  }
  onImportShowing(e) {
    this.valueProgress = 0;
  }

  onImportHiding(e) {
    let r = true;
    if (this.valueProgress < 100 && this.errorImport == false) {
      r = confirm("Cnh bo: Qu trnh ti ha n s dng li nu bn ng ca s ny?");
    }
    if (r) {
      if (this.subXuatHoaDon) {
        this.subXuatHoaDon.unsubscribe();
      }
      this.valueProgress = 0;
    } else {
      e.cancel = true;
    }
  }
  customizeText = (url) => {
    this.invoiceService.handleTotalGrid(url.value, this.objStatusBtn, this.dataGrid);
    return '';
  }
  calculateFilterExpression(filterValue, selectedFilterOperation) {
    selectedFilterOperation = "contains"; //Helper.multilfilterColumn;
    return [["template_code_temp", selectedFilterOperation || '=', filterValue],'or', ["receipt_series_temp",selectedFilterOperation || '=', filterValue]];
  }
  calculateFilterCustomerExpression(filterValue, selectedFilterOperation) {
    selectedFilterOperation = "contains"; //Helper.multilfilterColumn;
    return [["customer_name_temp", selectedFilterOperation || '=', filterValue],'or', ["payer_name_temp",selectedFilterOperation || '=', filterValue]];
  }
}
